import './App.css';
import { useState, useEffect, } from 'react';
import { ImCalculator} from 'react-icons/all';

function App() {

  const [result, setResult] = useState("");
  
  const calculate = () => {
    try {
      setResult(eval(result));
    } catch (e) {
      setResult ('error');
    }
  }

  const handleClick = (e) => {
    const value = e.target.getAttribute('value');
    console.log(value);
    switch(value) {
      case 'AC':
        setResult('');
        break;
      case "=" :
        calculate();
        break;
      case "DEL": 
        setResult(result.slice(0, -1));
        console.log("DEL");
        break;
      default:
        setResult(result + value);
        if (result.length > 21){
          setResult("Error: Max Digits Reached")
        }
   }
  }

  useEffect(() => {
  
     const handleKeyPress = (e) => {
      if (e.key === "1"){
        setResult(result + e.key)
     }
      else if (e.key === "2"){
        setResult(result +  e.key)
     }
      else if (e.key === "3"){
        setResult(result + e.key);
      }
      else if (e.key === "4"){
        setResult(result + e.key)
      }
      else if (e.key === "5"){
        setResult(result + e.key)
      }
      else if (e.key === "6"){
        setResult(result + e.key)
      }
      else if (e.key === "7"){
        setResult(result + e.key)
      }
      else if (e.key === "8"){
        setResult(result +e.key)
      }
      else if (e.key === "9"){
        setResult(result + e.key)
      }
      else if (e.key === "0"){
        setResult(result + e.key)
      }
      else if (e.key === "/"){
        setResult(result + "/")
      }
      else if (e.key === "*"){
        setResult(result + "*")
      }
      else if (e.key === "-"){
        setResult(result + "-")
      }
      else if (e.key === "+"){
        setResult(result + "+")
      }
      else if (e.keyCode === 8){
        setResult(result.slice(0, -1));
      }
      else if (e.keyCode === 46){
        setResult('');
      }
      else if (e.keyCode === 13 || e.keyCode === 187){
        calculate();
      }
   } 
    document.addEventListener("keydown", handleKeyPress);
    return () => {
      document.removeEventListener("keydown", handleKeyPress);
    };
    // eslint-disable-next-line
  }, []);

  return (
    <div className="App">
       <div className="container">
       <div className="logo-text"><ImCalculator className="App-logo"/>{" "}calc</div>
         <div type="text" className="result"><div className="textResult">{result}</div></div>
         <div className="row-1">
          <input type="button" value="AC" onClick={handleClick} style={{color:'#C84B31', fontSize: '18px'}} className="global" />
          <input type="button" value="DEL" onClick={handleClick} style={{color:'#C84B31', fontSize: '18px'}} className="global" />
          <input type="button" value="%"  onClick={handleClick} style={{color:'#C84B31', fontSize: '18px'}} className="global" />
          <input type="button" value="/"  onClick={handleClick} style={{color:'#C84B31'}} className="global" />
         </div>
         <div className="row-2">
           <input type="button"  onClick={handleClick} value="7" className="global" />
           <input type="button" value="8"  onClick={handleClick} className="global" />
           <input type="button" value="9"  onClick={handleClick} className="global" />
           <input type="button" value="*"  onClick={handleClick} style={{color:'#C84B31', fontSize: '25px'}} className="global" />
         </div>
         <div className="row-3">
           <input type="button" value="4"  onClick={handleClick} className="global" />
           <input type="button" value="5"  onClick={handleClick} className="global" />
           <input type="button" value="6"  onClick={handleClick} className="global" />
           <input type="button" value="-"  onClick={handleClick} style={{color:'#C84B31', fontSize: '25px'}} className="global" />
          </div>
          <div className="row-4">
            <input type="button"  onClick={handleClick} value="1" className="global" />
            <input type="button" value="2"  onClick={handleClick}className="global" />
            <input type="button" value="3"  onClick={handleClick} className="global" />
            <input type="button" value="+"  onClick={handleClick} style={{color:'#C84B31', fontSize: '25px'}} className="global" />
          </div>
          <div className="row-5">
           
            <input type="button" value="0"  onClick={handleClick} className="global" />
            <input type="button" value="."  onClick={handleClick} className="global" />
            <input type="button" value="="  onClick={handleClick} className="global equal" />
          </div>
         <div><p className="author">Designed and Coded by abroro</p></div>
      </div>
      
    </div>
  );
}


export default App;










































